.player {
    display: block;
    position: absolute;
    bottom: 130px;
    left: 10px;
}

.videotxt {
    font-family: Montserrat;
    position: relative;
    display: block;
    font-style: normal;
    font-weight: 600;
    font-size: 12px;
    line-height: 18px;
    display: flex;
    align-items: center;
    text-align: center;
    color: #DE8F75;
    top: 30px;
    left: 25px;
}

.playbtn {
    display: block;
    position: relative;
    top: 44px;
    left: 22px;
}

.center .slick-center h3 {
    color: #e67e22;
    opacity: 1;
    -ms-transform: scale(1.08);
    transform: scale(1.08);
}


.slick-dots li {
    position: relative !important;
    display: block !important;
    width: 90px !important;
    height: 20px !important;
    margin: 90px 5px !important;
    top: 250px !important;
    padding: 0 !important;
    cursor: pointer !important;
}

.slick-list img{
    width: 430px;
    height: 556px;
}

.slick-dots {
    position: absolute !important;
    bottom: 370px !important;
    width: 100% !important;
    padding: 0 !important;
    margin: 0 !important;
    list-style: none !important;
    text-align: center !important;
    
}

.slick-dots img{
    opacity: 0.5;
}

.slick-active img{
    opacity: 1;
}

.slick-list {
    position: relative !important;
    display: block !important;
    overflow: hidden !important;
    margin: 0 !important;
    padding: 0 !important;
    width: 430px !important;
    height: 556px !important;
    left: 200px !important;
    bottom: 105px !important;
}

.slick-prev {
    left: 40px !important;
    top: -16px !important;
    z-index: 9999;
}

.slick-next {
    top: 450px !important;
    right: 22px !important;
    transform: rotate(180deg) !important;
}

.slick-arrow {
    background-image: url("./files/arrow.png") !important;
    background-repeat: no-repeat !important;
    display: block;
    border: none;
    background: none;
    text-indent: 200%;
    white-space: nowrap;
    overflow: hidden;
    width: 24px;
}