@font-face {
    font-family: Montserrat;
    src: url(./font/Montserrat.ttf);
}
@font-face {
    font-family: Montserrat-Bold;
    src: url(./font/Montserrat-Bold.ttf);
}

.productsuges{
    position: absolute;
    display: flex;
    top: 930px;
}

.colorssuge {
    position: relative;
    display: flex;
    margin-left: 98px;
}

.colorssugeless {
    position: relative;
    display: flex;
    margin-left: 135px;  
}

.titlecard {
    position: absolute;
    display: flex;
    top: 870px;
    display: block;
    margin: 0 auto;
    width: 100%;
    max-width: 1200px;
    padding-right: 20px;
    padding-left: 20px;
}

.titlecard a {
    font-family: Montserrat;
    font-style: normal;
    margin-left: 460px;
    font-weight: bolder;
    font-size: 26px;
    line-height: 38px;
    display: flex;
    align-items: center;
    text-align: center;
    text-transform: uppercase;
    color: #DE8F75;
}

.product {
    margin-left: 13px;
}

.cardsuge {
    display: block;
    max-width: 290px;
    margin-right: 15px;
}

.infsuge {
    display: flex;
}

.pricesuge {
    font-family: Montserrat;
    font-style: normal;
    font-weight: bolder;
    font-size: 16px;
    line-height: 23px;
    display: flex;
    align-items: center;
    color: #202020;
}

.jazzberry {
    width: 13px;
    height: 13px;
    border-radius: 50%;
    background-color: #A9095E;
    margin-left: 25px;
}

.casal {
    width: 13px;
    height: 13px;
    border-radius: 50%;
    background-color: #2A5A75;
    margin-left: 15px;
}

.carmine {
    width: 13px;
    height: 13px;
    border-radius: 50%;
    background-color: #A14830;
    margin-left: 15px;
}

.black {
    width: 13px;
    height: 13px;
    border-radius: 50%;
    background-color: #000000;
    margin-left: 15px;
}

.backbtn {
    width: 35px;
    height: 35px;
    position: absolute;
    border: 2px solid #2A5A75;
    box-sizing: border-box;
    border-radius: 100%;
}

.backbtn img{
    width: 9px;
    height: 14px;
    margin: 9px;
    display: block;
    position: relative;
}

.navgation {
    display: block;
    position: absolute;
    top: 415px;
    left: 540px;
}

.nextbtn {
    width: 35px;
    height: 35px;
    position: absolute;
    left: 90px;
    border: 2px solid #2A5A75;
    box-sizing: border-box;
    border-radius: 100%;
}

.nextbtn img{
    width: 9px;
    height: 14px;
    margin: 9px;
    left: 2px;
    display: block;
    position: relative;
}

.infpage {
    display: block;
    position: absolute;
    text-align: center;
    width: 41px;
    top: 16px;
    left: 42px;
}

.infpage a {
    position: absolute;
    left: 0%;
    right: 0%;
    top: 0%;
    bottom: 0%;
    font-family: Open Sans;
    font-style: normal;
    font-weight: bolder;
    font-size: 14px;
    line-height: 23px;
    display: flex;
    align-items: center;
    text-align: center;
    color: #2A5A75;
}