@font-face {
    font-family: Montserrat;
    src: url(./navbarfiles/font/Montserrat.ttf);
}

ul {
    padding: 0px;
}

.conteiner {
    display: block;
    margin: 0 auto;
    width: 100%;
    max-width: 1200px;
  }

    .header {
        position: absolute;
        height: 86px;
        left: 0%;
        right: 0%;
        top: 0px;
        background: rgba(255, 255, 255, 0.9);
    }
        .logo {
            display: block;
            position: relative;
            margin: 26px auto;
        }

    .navbar {
        position: absolute !important;
        display: flex !important;
        height: 50px;
        left: 0%;
        right: 0%;
        top: 86px;
        background: rgba(255, 255, 255, 0.9);
        border-top-width: 1px;
        border-top-style: solid;
        border-top-color: #CCCCCC;
        border-bottom-width: 1px;
        border-bottom-style: solid;
        border-bottom-color:#CCCCCC;
        flex-wrap: inherit !important;
    }
        .navbar ul {
            list-style: none;
        }
        
        .navbar ul li { 
            display: inline;
            margin: 10px;    
        }
        
        .sing {
            padding-left: 40px;
            min-width: 250px;
        }

        .sing ul li a{
            text-decoration: none;
            font-family: Montserrat;
            font-style: normal;
            font-weight: normal;
            font-weight: 600;
            font-size: 12px;
            line-height: 18px;
            align-items: center;
            color: #686868;            
        }

        .menu {
            width: 690px;
        }

        .menu2 {
            min-width: 160px;
            margin-left: 100px;
            display: flex;
            position: relative;
            bottom: 10px;
        }

        .menu ul li a{
            text-decoration: none;
            font-family: Montserrat;
            font-style: normal;
            font-weight: normal;
            font-weight: 600;
            font-size: 14px;
            line-height: 18px;
            align-items: center;
            color: #686868;
            margin-left: 44px;
        }

        .searchbox {
            margin: 7px;
            width: 83px;
            height: 36px;
            float: right;
            display: inline-flex;
            border-bottom-width: 1px;
            border-bottom-style: solid;
            border-bottom-color: #CCCCCC;
        }
            .search {
                margin-top: 10px;
                max-height: 21px;
                max-width: 21px;
            }
            .searchtxt p{
                font-family: Montserrat;
                font-style: normal;
                font-weight: normal;
                font-weight: 600;
                font-size: 12px;
                line-height: 18px;
                display: flex;
                align-items: center;
                color: #686868;
                margin: 0 auto;
            }
            
            .searchtxt {
                margin-left: 17px;
                margin-top: 10px;
            }

        .cartbox {
            display: inline-flex;
            margin: 7px;
            margin-left: 15px;
            float: right;
        }
            .cart {
                margin-top: 8px;
                max-height: 24px;
                max-width: 27px;
            }
            .carttxt {
                margin-left: 3px;
                margin-top: 15px;
            }
            .carttxt p{
                font-family: Montserrat;
                font-style: normal;
                font-weight: normal;
                font-weight: 600;
                font-size: 12px;
                line-height: 19,61px;
                display: flex;
                align-items: center;
                color: #686868;
                margin: 0 auto;
            }

            .btncart {
                background-color: transparent !important;
                border-color: transparent !important;
                border-radius: 3px !important;
                padding: 0 !important;
            }

 .modbody .modal-content {
    width: 525px !important;
    height: 100% !important;
    float: right !important;
    position: fixed !important;
    display: block !important;
    right: 0px !important;
    top: 0px !important;
    border-radius: 0px !important;
}

.navmodalheader a{
    font-family: Montserrat;
    font-style: normal;
    font-weight: bolder;
    font-size: 26px;
    line-height: 163.42%;
    display: block;
    align-items: center;
    text-transform: uppercase;
    color: #DE8F75 !important;
}

.navmodalheader p{
    
    font-family: Montserrat;
    font-style: normal;
    font-weight: bolder;
    font-size: 18px;
    
    display: block;
    
    color: #AEB6C1 !important;
}

.navmodalheader.modal-header {
    border-bottom: 1px solid #dee2e6 !important;
    width: 92%;
    margin-left: 23px;
    display: block;
}

.navmodalheader .close {
    left: 20px;
    position: relative;
    bottom: 90px;
}

.modalbody {
    display: block;
    margin-top: 30px;
}

.modalbody .modalimg {
    display: block;
    margin-bottom: 36px;
}

.freeship {
    position: fixed;
    bottom: 90px;
    width: 100%;
    height: 50px;
    background: #AEB6C1;
    text-align: center;
}

.freeship a {
    margin-top: 14px;
    display: inline-block;
    position: absolute;
    font-family: Montserrat;
    font-style: normal;
    font-weight: normal !important;
    font-size: 14px;
    line-height: 163.42%;
    color: #FFFFFF !important;
    left: 113px;
}

.freeship p {
    font-family: Montserrat;
    font-style: normal;
    font-weight: bold !important;
    font-size: 14px;
    line-height: 20px;
    display: inline;
    align-items: center;
    color: #FFFFFF !important;
}

.modbody .buybtn {
    background: #32917B !important;
    border-radius: 3px !important;
    border-color: #32917B !important;
    position: relative;
    width: 235px;
    height: 42px;
    top: 5px;
    left: 260px;
}

.modbody .buybtn a {
    font-family: Montserrat;
    font-style: normal;
    font-weight: bolder;
    font-size: 12px;
    line-height: 20px;
    display: flex;
    align-items: center;
    text-align: center;
    text-transform: uppercase;
    color: #FFFFFF !important;
    margin-left: 42px;
}

.modfooter {
    position: fixed;
    bottom: 0;
    width: 100%;
    height: 90px;
    background: #E3E3E3;
}

.infprod {
    display: inline-flex;
}

.moreprod {
    display: inline-flex;
    max-height: 20px;
}

.moreprod .mais {
    width: 18px ;
    height: 18px ;
}

.moreprod a {
    font-family: Montserrat;
    font-style: normal;
    font-weight: bolder !important;
    font-size: 14px;
    line-height: 17px;
    align-items: center;
    text-align: center;
    color: #5F75AA !important;
    margin: 0px 12px 12px;
}

.modal-backdrop {
    width: 100% !important;
    height: 100% !important;
}

.moreprod .menos {
    width: 18px ;
    height: 4px ;
    margin: auto;
}

.infprodmod {
    width: 190px;
    margin-left: 40px;
}

.infprodmod a {
    font-family: Montserrat;
    font-style: normal;
    font-weight: bold !important;
    font-size: 12px;
    line-height: 122.56%;
    align-items: center;
    color: #202020 !important;
    
}

.infprodmod p {
    font-family: Montserrat;
    font-style: normal;
    font-weight: 700 !important;
    font-size: 14px;
    line-height: 20px;
    align-items: center;
    color: #000000 !important;
}

.removeprod {
    margin-left: 100px;
    margin-top: -5px;
}

.priceinfo {
    display: block;
    position: relative;
    bottom: 40px;
    max-width: 200px;
    margin-left: 40px;
}

.priceinfo a {
    font-family: Montserrat;
    font-style: normal !important;
    font-weight: 800 !important;
    font-size: 16px !important;
    line-height: 23px !important;
    color: #202020 !important;
}

.priceinfo p {
    font-family: Montserrat;
    font-style: normal !important;
    font-weight: 700 !important;
    font-size: 12px !important;
    line-height: 18px !important;
    color: #000000 !important;    
}